html {
  box-sizing: border-box; }

html {
  height: 100%; }

body {
  min-height: 100%;
  background-color: #444444;
  background-repeat: round;
  background-size: 10px; }

*, *:before, *:after {
  box-sizing: inherit; }

.main {
  display: flex;
  justify-content: center;
  padding: 20px 5px; }

.repositories {
  display: flex;
  flex-direction: column;
  padding: 0px 20px 20px 20px;
  -webkit-backdrop-filter: blur(2px);
          backdrop-filter: blur(2px);
  border: #9cdaf0 solid 1px;
  border-radius: 10px;
  flex-grow: 0;
  width: 100%;
  max-width: 500px; }
  .repositories__input-wrapper {
    z-index: 99;
    height: 40px;
    overflow: visible; }
  .repositories__search {
    background-color: white;
    font-family: 'Roboto';
    font-weight: 300;
    width: 100%;
    height: 40px;
    padding: 5px 20px;
    border: #9cdaf0 solid 2px;
    border-radius: 10px;
    margin-bottom: 5px; }
    .repositories__search:focus {
      outline: #9cdaf0 solid 3px; }
  .repositories__datalist {
    font-family: 'Roboto';
    padding: 0;
    margin: 0;
    font-weight: 300;
    display: flex;
    flex-direction: column;
    width: 100%;
    background-color: white;
    overflow: hidden;
    max-height: 0px;
    transition: max-height 0.15s ease-in-out;
    border-radius: 10px; }
  .repositories__option {
    font-family: "Roboto";
    font-weight: 300;
    word-wrap: break-word;
    padding: 10px 20px;
    cursor: pointer; }
    .repositories__option:hover {
      background-color: #9cdaf0; }
  .repositories__img {
    margin: 20px auto;
    width: 200px; }
  .repositories__list {
    list-style: none;
    margin: 0;
    padding: 0;
    display: flex;
    flex-direction: column; }
  .repositories__item {
    padding: 10px 20px;
    background-color: #9cdaf0;
    overflow: hidden;
    display: flex;
    max-height: 0px;
    flex-direction: column;
    transform: rotateX(-90deg);
    transition: max-height 0.2s, transform 1s, padding 1s, margin 1s;
    transform-origin: 50% 0;
    border-radius: 10px;
    margin-bottom: 5px; }
  .repositories__span {
    word-wrap: break-word;
    font-family: 'Roboto';
    font-weight: 400;
    margin-left: 50px;
    margin-right: 30px; }
    .repositories__span::before {
      font-family: 'Roboto';
      font-weight: 300;
      position: absolute;
      left: 10px; }
  .repositories__name::before {
    content: 'Name: '; }
  .repositories__owner::before {
    content: 'Owner: '; }
  .repositories__stars::before {
    content: 'Stars: '; }
  .repositories__button {
    border: none;
    background-color: transparent;
    cursor: pointer; }
  .repositories__button--close {
    position: absolute;
    top: 10px;
    right: 10px;
    background-color: red;
    background-image: url(./static/close.png);
    background-size: 30px 30px;
    width: 30px;
    height: 30px;
    border-radius: 50%; }
    .repositories__button--close:hover {
      -webkit-filter: hue-rotate(20deg);
              filter: hue-rotate(20deg); }
  .repositories__error {
    color: red;
    font-family: 'Roboto';
    font-weight: bold;
    padding: 20px;
    justify-self: center; }
    .repositories__error::before {
      content: 'Error: ';
      color: red;
      font-family: 'Roboto';
      font-weight: 400; }
  .repositories__loading {
    width: 50px;
    margin: 10px auto; }
